import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import MattlaggningCarousel from "../components/mattlaggningCarousel"
import { graphql, useStaticQuery } from "gatsby"
import { ParallaxProvider} from "react-scroll-parallax"
import ParallaxBanner from "../components/parallaxBanner"

const MattlaggningPage = () => {
    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            mattlaggningHeroes {
                hero {
                  url
                }
              }
        }
    }
  `)

    return (
        <Layout>
            <SEO title="Mattläggning" />
            <ParallaxProvider>
                <div class="cntnr column-flex">

                    <ParallaxBanner url={gcms.mattlaggningHeroes[0].hero.url} heading="Mattläggning"/>

                    <section class="content-section">
                    <h1 class="section-header">Mattläggning</h1><br/>
                        <p>
                            Med vår långa erfarenhet i branschen vet vi på Victoria golv vad som krävs
                            för en optimal mattläggning. I dagsläget finns ett stort utbud med variation
                            och design av heltäckande mattor. Ett mjukt och anpassad textilgolv ger en
                            mer hemtrevlig och välkomnande inredning i tex sovrum och hall.
                            Behöver du lägga någon form av matta till ditt företag eller hem?
                            Tillsammans hjälper vi dig att hitta den perfekta heltäckande mattan
                            som passar för just dig.

                    </p>
                    </section>
                    <section
                        class="slide-section">
                        <MattlaggningCarousel />
                    </section>
                </div>
            </ParallaxProvider>
        </Layout>
    )
}

export default MattlaggningPage;